import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import posts from './modules/posts'

Vue.use(Vuex)

export default new Vuex.Store({
  actions,
  modules: {
    posts
  },
  strict: process.env.NODE_ENV !== 'production'
})



