<template>
  <div class="container single">
    <div v-if="post">
      <div class="single-navigation header-fix-bar">
        <router-link v-if="post.next_post" :to="{ name: 'single', params: { slug: post.next_post.post_name }  }" class="next">
          &larr;<span v-html="post.next_post.post_title" class="hide-mobile"></span>
          <span class="show-mobile">Newer post</span>
        </router-link>

        <router-link v-if="post.prev_post" :to="{ name: 'single', params: { slug: post.prev_post.post_name }  }" class="prev">
          <span class="show-mobile">Older post</span>
          <span v-html="post.prev_post.post_title" class="hide-mobile"></span>&rarr;
        </router-link>
      </div>
    </div>
    <div class="posts">
      <div class="not-found" v-if="post == 0">
        <p>Post not found!</p>
      </div>
      <article class="post" v-if="post">
        <h1 class="post-title">
          <router-link :to="{ name: 'single', params: { slug: post.slug } }">
            <span v-html="post.title.rendered"></span>
          </router-link>
        </h1>
        <div class="post-meta">
          <span class="time" v-text="post.date"></span>
        </div>
        <div class="post-content" v-html="post.content.rendered"></div>
      </article><!-- end .post -->

      <comments :post="post.id" :comments-number="post.comments_number" v-if="post"></comments>
    </div>
  </div>
</template>

<script>
  import posts from './../store/api/posts.js'
  import Comments from 'components/Comment/List.vue'
  export default {
    components: {
      Comments
    },
    data () {
      return {
        post: null
      }
    },
    created () {
      this.loadItem()
    },
    watch: {
      $route (to, from) {
        this.loadItem()
      }
    },
    methods: {
      loadItem () {
        posts.getPost(this.$route.params.slug)
        .then((response) => {
          this.post = response.data[0]
          document.title = this.post.title.rendered + ' | The Mopho Group '
        })
        .catch((error) => {
          console.log(error)
        })
      }
    }
  }
</script>


